.button{
  margin: 0 auto;
  color: #fff;
  font-family: Montserrat,sans-serif;
  font-size: 13px;
  font-weight: 700;
  background-color: inherit;
  width: 100px;
  height: 30px;
  border: solid 2px #fff;
  border-radius: 40px;
  &_down{
    width: 30px;
  }
}
.button:hover {background-color: #17a2b8;}
